[data-theme=dark] .frappe-card .btn-default {
  background-color: var(--bg-color);
}
[data-theme=dark] .frappe-card .btn-default:hover {
  background-color: var(--fg-hover-color);
}
[data-theme=dark] .modal,
[data-theme=dark] .form-in-grid {
  --control-bg: var(--gray-800);
  --border-color: var(--gray-600);
}
[data-theme=dark] .print-format {
  --text-color: var(--gray-900);
  --border-color: var(--gray-100);
}
[data-theme=dark] .ql-editor {
  color: var(--text-on-gray);
}
[data-theme=dark] .ql-editor.read-mode span:not(.mention),
[data-theme=dark] .ql-editor.read-mode p,
[data-theme=dark] .ql-editor.read-mode u,
[data-theme=dark] .ql-editor.read-mode strong {
  background-color: inherit !important;
  color: inherit !important;
}
[data-theme=dark] .chart-container {
  --charts-label-color: var(--gray-300);
  --charts-axis-line-color: var(--gray-500);
  --charts-stroke-width: 5px;
  --charts-dataset-circle-stroke: #ffffff;
  --charts-dataset-circle-stroke-width: var(--charts-stroke-width);
  --charts-tooltip-title: var(--charts-label-color);
  --charts-tooltip-label: var(--charts-label-color);
  --charts-tooltip-value: white;
  --charts-tooltip-bg: var(--gray-900);
  --charts-legend-label: var(--charts-label-color);
}
[data-theme=dark] .heatmap-chart g > rect[fill="#ebedf0"] {
  fill: var(--gray-700);
}
[data-theme=dark] .rating {
  --star-fill: var(--gray-500);
}
[data-theme=dark] .rating .star-hover {
  --star-fill: var(--gray-400);
}
[data-theme=dark] ::-moz-selection {
  color: var(--text-color);
  background: var(--gray-500);
}
[data-theme=dark] ::selection {
  color: var(--text-color);
  background: var(--gray-500);
}

input[type=checkbox] {
  position: relative;
  width: var(--checkbox-size) !important;
  height: var(--checkbox-size);
  margin-right: var(--checkbox-right-margin) !important;
  background-repeat: no-repeat;
  background-position: center;
  border: 1px solid var(--gray-400);
  box-sizing: border-box;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-print-color-adjust: exact;
  color-adjust: exact;
}
.grid-static-col input[type=checkbox] {
  margin-right: 0 !important;
}
input[type=checkbox]:checked {
  background-color: var(--primary);
  background-image: url("data:image/svg+xml, <svg viewBox='0 0 8 7' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M1 4.00001L2.66667 5.80001L7 1.20001' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/></svg>"), var(--checkbox-gradient);
  background-size: 57%, 100%;
  box-shadow: none;
  border: none;
}
input[type=checkbox]:focus {
  outline: none;
  box-shadow: var(--checkbox-focus-shadow);
}
input[type=checkbox].disabled-deselected, input[type=checkbox]:disabled {
  background-color: var(--disabled-control-bg);
  box-shadow: inset 0px 1px 7px rgba(0, 0, 0, 0.1);
  border: 0.5px solid var(--gray-300);
  pointer-events: none;
}
input[type=checkbox].disabled-selected, input[type=checkbox]:disabled:checked {
  background-color: var(--gray-500);
  background-image: url("data:image/svg+xml, <svg viewBox='0 0 8 7' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M1 4.00001L2.66667 5.80001L7 1.20001' stroke='white' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/></svg>");
  background-size: 57%;
  box-shadow: inset 0px 1px 3px rgba(0, 0, 0, 0.1);
  border: none;
  pointer-events: none;
}

input[type=radio] {
  position: relative;
  width: var(--checkbox-size) !important;
  height: var(--checkbox-size);
  margin-right: 8px !important;
  font-size: calc(var(--checkbox-size) - 1px);
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-print-color-adjust: exact;
  color-adjust: exact;
}
input[type=radio]:focus {
  outline: none;
}
input[type=radio]:before {
  width: var(--checkbox-size);
  height: var(--checkbox-size);
  position: absolute;
  content: " ";
  border: 1px solid var(--gray-400);
  border-radius: 16px;
}
input[type=radio]:checked::before {
  background-color: var(--primary);
  border-radius: 16px;
  box-shadow: inset 0 0 0 2px white;
}

html, body {
  height: 100%;
}

/* checkbox */
.checkbox {
  --checkbox-right-margin: 8px;
}
.checkbox label {
  display: inline-flex;
  align-items: center;
  margin-bottom: 0;
}
.checkbox .label-area {
  font-size: var(--text-sm);
}
.checkbox .input-area, .checkbox .disp-area {
  display: flex;
}

.frappe-card {
  border-radius: var(--border-radius-md);
  box-shadow: var(--card-shadow);
  padding: var(--padding-sm);
  background-color: var(--card-bg);
}

.frappe-control[data-fieldtype=Select].frappe-control[data-fieldname=color] .color-select {
  padding-left: 40px;
}
.frappe-control[data-fieldtype=Select].frappe-control[data-fieldname=color] .color-box {
  position: absolute;
  top: calc(50% - 11px);
  left: 8px;
  width: 22px;
  height: 22px;
  border-radius: 5px;
  z-index: 1;
}

.frappe-control[data-fieldtype=Select] .control-input,
.frappe-control[data-fieldtype=Select].form-group {
  position: relative;
}
.frappe-control[data-fieldtype=Select] .control-input select,
.frappe-control[data-fieldtype=Select].form-group select {
  /* for Firefox */
  -moz-appearance: none;
  /* for Chrome */
  -webkit-appearance: none;
}
.frappe-control[data-fieldtype=Select] .control-input .select-icon,
.frappe-control[data-fieldtype=Select].form-group .select-icon {
  padding-left: inherit;
  padding-right: inherit;
  position: absolute;
  pointer-events: none;
  top: 7px;
  right: 12px;
}
.frappe-control[data-fieldtype=Select] .control-input .select-icon.xs,
.frappe-control[data-fieldtype=Select].form-group .select-icon.xs {
  top: 3px;
